.school-container-title {
    padding-top: 40px;
}

.school-container {
	width: 100vw;
	min-height: 100vh;
	display: flex;
	justify-content: center;
	align-items: flex-start;
	flex-wrap: wrap;
    padding: 0 0 20px 0;
}

.school-container .search {
	width: 84%;
	height: 50px;
	border-radius: 10px;
	overflow: hidden;
	border: 2px solid #23225E;
	margin-top: 10px !important;
    margin-bottom: 30px;
}

.school-container .search:focus-within {
    border-width: 3.4px;
}

.school-container .search > input {
	width: 100%;
	height: 100%;
	outline: none;
	padding: 10px;
	border: none;
	font-size: 30px;
	color: #23225E ;

}

.school-container a {
    display: inline-block;
    width: 20%;
    color: #FFFFFF;
    color: #EBDB80;
    margin: 10px;
    text-decoration: none;
}

.school-container .box {
	background: #23225E;
	border-radius: 10px;
	display: flex;
    flex-wrap: wrap;
	justify-content: space-around;
	align-items: center;
	padding: 10px;
	overflow: hidden;
}

.school-container .box h2 {
    width: 100%;
    text-align: center;
    margin: 0;
}

.school-container .icon {
	width: calc(6vw + 35px);
    height: calc(6vw + 35px);
}

.school-container h1 {
	font-size: calc(3vw + 30px);
    margin: 0;
}

@media screen and (max-width: 1200px) {
	.school-container a {
		width: 25%;
	}
	.school-container .search {
		width: 79%;
	}
}

@media screen and (max-width: 850px) {
	.school-container a {
		width: 40%;
	}
	.school-container .search {
		width: 82.7%;
	}
}
@media screen and (max-width: 600px) {
	.school-container .search {
		width: 83%
	}
}


@media screen and (max-width: 450px) {
	.school-container a {
		width: 90%;
	}

	.school-container .search {
		width: 90%;
	}
}