section.Panel.top-bar-panel {
	margin: 0 !important;
	border-bottom-left-radius: unset !important;
	border-bottom-right-radius: unset !important;
}

.top-bar {
	overflow: hidden;
}

.banner {
	width: 100%;
	height: 83vh;
	background-size: cover;
	background-position: center;
	background-repeat: repeat;
	position: relative;
}

.banner::before {
	content: '';
	position: absolute;
	inset: 0;
	background-color: #6400fa99;
	background-color: #5823d299;
}

@media screen and (max-width: 790px) {
	.banner {
		background-position: 25%;
	}
}

.banner > .content {
	position: absolute;
	background: rgba(5, 10, 20, 0.699);
	backdrop-filter: blur(5px);
	z-index: 1;
	height: 60px;
	top: 0;
	width: 100%;
	display: flex;
	align-items: center;
	justify-content: center;
	overflow-x: hidden;
}

.banner > .content::-webkit-scrollbar {
	width: 0;
}

.banner > .content .item-wrapper {
	display: flex;
	justify-content: center;
	align-items: center;
	column-gap: 20px;
	height: 100% !important;
}

@media screen and (max-width: 600px) {
	.banner > .content .item-wrapper {
		animation: translateX 15s linear infinite;
	}
}

@keyframes translateX {
	0% {
		transform: translateX(-100%);
	}
	100% {
		transform: translateX(100%);
	}
}

.banner > .content .item {
	flex-shrink: 0;
	display: flex;
	align-items: center;
}

.banner > .content .item > * {
	color: #fff;
	padding: 0 8px;
	font-weight: 300;
}