.fixed-navbar-menu {
	display: none;
}

.fixed-navbar-menu a {
	position: relative;
	color: #FFFFFF;
	display: flex;
	text-decoration: none;
	flex-direction: column;
	align-items: center;
	transition: all .2s;
}

.fixed-navbar-menu a > i.icon {
	background-color: #FFFFFF;
} 

.fixed-navbar-menu a.active {
	color: #eccb12;
	transform: scale(1.1);
}

.fixed-navbar-menu a.active > i.icon {
	background-color: #eccb12;
	/* background-color: #23225E; */
}

@media screen and (max-width: 900px) {
	.fixed-navbar-menu {
		display: flex;
		position: fixed;
		bottom: 0;
		left: 0;
		z-index: 99;
		background-color: #040323c0;
		backdrop-filter: blur(20px);
		width: 100%;
		padding: 8px 0;
		justify-content: space-around;
	}
}