section.Panel.navbar-panel {
	margin-top: 0;
    border-top-left-radius: unset;
    border-top-right-radius: unset;
    position: sticky;
	position: -webkit-sticky;
    top: -20px;
    z-index: 999;
	background-color: #ffffffaa;
	box-shadow: 0 0 5px gray;
}

.navbar
{
	display: flex;
	flex-wrap: wrap;
	align-items: center;
	justify-content: space-between;
	padding: 10px 20px;
	background-color: #ffffff88;
	backdrop-filter: blur(5px);
}

.navbar > .logo 
{
	display: flex;
	align-items: center;
}

.navbar > .logo > img 
{
	width: calc(3vw + 30px);
	min-width: 50px;
	border-radius: 50%;
}

.navbar .links {
	height: 100%;
	display: flex;
	gap: calc(2vw + 10px);
	justify-content: space-between;
	align-items: center;
	position: relative;
}

.navbar .links > a {
	display: inline-block;
	text-decoration: none;
	text-align: center;
	display: flex;
	justify-content: center;
	align-items: center;
	position: relative;
}

.navbar .links > a > h4 {
	color: #23225E;
	padding: 5px 10px;
	display: flex;
	column-gap: 5px;
	font-weight: 300;
}

.navbar .links > a::before {
	content: '';
	height: 0;
	opacity: 0;
	width: 60px;
	transition: all .4s;
	position: absolute;
	left: 50%;
	bottom: 5px;
	transform: translateX(-50%);
	background: #eccb12;
	border-radius: 2px;
}

.navbar .links > a.active h4 {
	color: #eccb12;
}

.navbar .links > a.active h4 i.icon {
	background: #eccb12;
}

.navbar .links > a.active::before {
	height: 2px;
	opacity: 1;
}

.navbar > .language {
	display: flex;
}

.navbar > .language #language {
	border-radius: 10px;
	background-color: #23225E;
	outline: 2px solid;
	padding: 4px 18px;
	color: #d3bc3a;
}


@media screen and (max-width: 420px) {
	.navbar > .language {
		order: 1;
	}
	.navbar > .links {
		order: 2;
		width: 100%;
	}
}

@media screen and (max-width: 900px) {
	.main-background > section.Panel.navbar-panel {
		position: static;
	}

	.navbar .links {
		display: none;
	}
}