:root {
	--box-margin: 10px;
	--icon-size: 25px;
}

html,
body {
	overflow: hidden;
}

body {
	margin: 0;
	font-size: 1rem;
	font-weight: 400;
	color: #212529;
	background-color: #fff;
	-webkit-text-size-adjust: 100%;
	-webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}

h6,
.h6,
h5,
.h5,
h4,
.h4,
h3,
.h3,
h2,
.h2,
h1,
.h1 {
	margin-top: 0;
	margin-bottom: 0.5rem;
	font-weight: 500;
	line-height: 1.2;
}

h1,
.h1 {
	font-size: calc(1.375rem + 1.5vw);
}

@media (min-width: 1200px) {

	h1,
	.h1 {
		font-size: 2.5rem;
	}
}

h2,
.h2 {
	font-size: calc(1.325rem + 0.9vw);
}

@media (min-width: 1200px) {

	h2,
	.h2 {
		font-size: 2rem;
	}
}

h3,
.h3 {
	font-size: calc(1.3rem + 0.6vw);
}

@media (min-width: 1200px) {

	h3,
	.h3 {
		font-size: 1.75rem;
	}
}

h4,
.h4 {
	font-size: calc(1.275rem + 0.3vw);
}

@media (min-width: 1200px) {

	h4,
	.h4 {
		font-size: 1.5rem;
	}
}

h5,
.h5 {
	font-size: 1.25rem;
}

h6,
.h6 {
	font-size: 1rem;
}

p {
	margin-top: 0;
	margin-bottom: 1rem;
}

* {
	padding: 0;
	margin: 0;
	box-sizing: border-box;
	font-family: Arial;
}

*::selection {
	background-color: #23225E;
	color: #D9BF60;
}

.main-background {
	background: #AEC4EB;
	width: 100%;
	height: 100vh;
	padding: 15px;
	overflow-y: scroll;
	scroll-behavior: smooth;
}

@media screen and (max-width: 860px) {
	.main-background {
		padding: 5px;
	}
}

@media screen and (max-width: 600px) {
	.main-background {
		padding-bottom: 45px;
	}
}

i.icon {
	vertical-align: middle;
	display: inline-block;
	width: var(--icon-size);
	height: var(--icon-size);
	-webkit-mask-repeat: no-repeat;
	mask-repeat: no-repeat;
	-webkit-mask-position: left top;
	mask-position: left top;
	-webkit-mask-size: 100%;
	mask-size: 100%;
	mask-mode: luminance;
}

i.icon.gold-icon {
	background-color: #EBDB80;
}

i.icon.white-icon {
	background-color: #FFF;
}

i.icon.red-icon {
	background-color: #ee1111;
}

i.icon.black-icon {
