/*--------------------------------------------------------------
# Sections General
--------------------------------------------------------------*/
section {
	overflow: hidden;
}

.section-bg {
	background-color: #f8fbfe;
}

.section-title {
	text-align: center;
	padding-bottom: 30px;
}

.section-title h2 {
	font-size: 30px;
	font-weight: bold;
	text-transform: uppercase;
	margin-bottom: 20px;
	padding-bottom: 0;
	color: #124265;
}

.section-title p {
	margin-bottom: 0;
	font-size: 14px;
	color: #919191;
}

.swiper-button-next, .swiper-button-prev {
	color: #23225E;
	font-weight: 900;
}