/* Posts Style */
.post-panel {
    width: 100%;
    min-height: 10vh;
    display: flex;
    justify-content: flex-start;
    align-items: flex-start;
    flex-wrap: wrap;
    padding: 30px 20px;
}

.posts-wrapper {
    display: flex;
    flex-wrap: wrap;
}

.post {
    background-color: #ffffff;
    width: calc(25% - 20px);
    margin: 10px;
    padding: 5px;
    border-radius: 6px;
    box-shadow: 1px 1px 2px rgb(71, 73, 75),
        -1px -1px 2px rgb(161, 151, 167);
}

.post:hover {
    box-shadow: 1px 1px 8px rgb(71, 73, 75),
        -1px -1px 8px rgb(161, 151, 167);
}

.post-image-wrapper {
    padding: 5px;
    overflow: hidden;
}

.post-image {
    width: 100%;
    border-radius: 6px 6px 0 0;
    overflow: hidden;
    aspect-ratio: 10/7;
    background: #23225E;
    position: relative;
    margin-bottom: 5px;
}

.post-image-like,
.post-image-open-btn {
    cursor: pointer;
    position: absolute;
    right: 10px;
    padding: 5px;
    bottom: 10px;
    background: #23225E88;
    border-radius: 8px;
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 10;
}

.post-image-like {
    right: unset;
    left: 10px;
}

.post-image>img {
    border: 1px solid #3d3b3b;
    height: 100%;
    width: 100%;
    object-fit: cover;
    position: absolute;
    top: 0;
    left: 0;
    transition: transform .3s;
}

.post:hover .post-image>img {
    transform: scale(1.06);
}

.post .title {
    font-weight: 600;
    font-size: 14px;
    padding: 2px 5px;
    overflow: hidden;
    text-overflow: ellipsis;
    -webkit-line-clamp: 3;
    display: -webkit-box;
    -webkit-box-orient: vertical;
    line-height: 1.2;
    height: calc(14px*3*1.2 + 4px);
}

.post .time-views {
    display: flex;
    flex-wrap: wrap;
    padding: 0 5px;
    justify-content: space-between;
    align-items: center;
    font-size: 14px;
}

.post .time-views .views {
    display: flex;
    align-items: center;
    column-gap: 3px;
}

.post .time-views .views .icon {
    --icon-size: 20px;
}

@media (min-width: 400px) {
    .post .title {
        font-size: calc(12px + (16 - 12)*(100vw - 400px)/(1200 - 400));
        height: calc(2px + 1.2*3*(12px + (16 - 12)*(100vw - 400px)/(1200 - 400)));
    }

    .post .time-views {
        font-size: calc(10px + (16 - 12)*(100vw - 400px)/(1000 - 400));
    }

    .post .time-views .views .icon {
        --icon-size: calc(16px + (16 - 12)*(100vw - 400px)/(1000 - 400));
    }
}

.post-modal {
    position: fixed;
    justify-content: center;
    width: 100vw;
    height: 100vh;
    background: #000000db;
    -webkit-backdrop-filter: blur(10px);
    backdrop-filter: blur(10px);
    z-index: 99999 !important;
    overflow-y: scroll;
    transition: all .6s cubic-bezier(.01, .5, .38, 1.24);
}

.post-modal::-webkit-scrollbar {
    width: .8vw;
}

.post-modal::-webkit-scrollbar-track {
    box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
}

.post-modal::-webkit-scrollbar-thumb {
    border-radius: 40px;
    background-color: darkgrey;
    outline: 1px solid slategrey;
}

.post-modal.close {
    transform: translate(-50%, -50%) scale(0);
}

.post-modal.open {
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%) scale(1);
}

.modal-close-icon {
    width: 40px;
    height: 40px;
    position: sticky;
    top: 20px;
    left: calc(100% - 60px);
    cursor: pointer;
    display: flex;
    align-items: center;
    filter: drop-shadow(0 0 4px red);
}

.modal-close-icon::after,
.modal-close-icon::before {
    content: '';
    display: inline-block;
    position: absolute;
    width: 40px;
    height: 6px;
    background: #fff;
    border-radius: 5px;
    transform: rotateZ(45deg);
}

.modal-close-icon::before {
    transform: rotateZ(-45deg);
}

.modal-container {
    margin: auto;
    width: 100%;
    max-width: 800px;
    min-height: 100vh;
    color: #ffffff;
    text-shadow: 0 0 1px #8f8fe0;
    font-family: 'Times New Roman', 'Courier New', Courier, monospace;
    padding: 60px 10px;
}

.modal-container img {
    display: inline-block;
    margin: 5px 0;
    background-color: #23225E;
    width: 100%;
    min-height: 100px;
    border: 1px solid #8f8fe0;
}

.modal-container .tags-time-views {
    display: flex;
    flex-wrap: wrap;
    column-gap: 30px;
    padding-bottom: 40px;
    padding-top: 10px;
}

.modal-container .tags-time-views .views {
    display: flex;
    align-items: center;
    column-gap: 4px;
}

.modal-container .title {
    padding-bottom: 20px;
}

.modal-container .text-content {
    text-indent: 30px;
    line-height: 1.3;
    word-spacing: 2px;
    padding: 5px 0;
    font-size: 18px;
}

.modal-container .text-content:last-child {
    padding-bottom: 20px;
}

.modal-container .hashtags {
    width: 100%;
    align-items: flex-end;
}

.modal-container .hashtags a {
    display: inline-block;
    padding: 0 5px;
    text-decoration: none;
    color: blue;
    font-size: calc(.671563097rem + .3971563097vw);
}

@media screen and (max-width: 1313px) {
    .main-background {
        padding: 10px;
        padding-bottom: 5px
    }
}

@media screen and (max-width: 1000px) {
    .post-panel {
        padding: 10px;
    }

    .post {
        width: calc(100%/3 - 20px);
    }
}

@media screen and (max-width: 680px) {
    .post {
        width: calc(100%/2 - 20px);
    }

    .main-background {
        padding: 6px;
    }
}

@media screen and (max-width: 480px) {
    .post {
        margin: 8px;
        width: calc(100%/2 - 16px);
    }
}

@media screen and (max-width: 440px) {
    .post-panel {
        padding: 10px;
    }
}

@media screen and (max-width: 380px) {
    .post-panel {
        padding: 5px;
    }
    .post {
        width: calc(100% - 12px);
    }

    .post .title {
        overflow: hidden;
        display: block;
        line-height: 1.2;
        height: auto;
    }
}